/**
 * @file 服务进程追踪
 */
@import '../../assets/styles/core/base';
@import '../../assets/styles/core/setting';
@import '../../assets/styles/core/reset';

.process_tracking_container {
  padding: 0 pr2(20);
  .process_state {
    font-size: pr2(22);
    font-family: PingFangSC-Medium;
    font-weight: 700;
    color:rgba(255,102,0,1);
    line-height: 1;
    margin: pr2(16) 0;
  }
  .content_wrapper {
    width: 100%;
    padding: 0 pr2(20) pr2(20) pr2(20);
    box-shadow: 0 pr2(4) pr2(20) 0 rgba(17,30,54,0.06);
    border-radius: pr2(4);
    box-sizing: border-box;
    .tab_wrapper {
      width: 100%;
      display: flex;
      height: pr2(44);
      align-items: center;
      margin-bottom: pr2(16);
      >li {
        height: 100%;
        display: flex;
        align-items: center;
        font-size: pr2(14);
        font-family: PingFangSC-Regular;
        font-weight: 400;
        color: rgba(102,109,127,1);
        line-height: 1;
        margin-right: pr2(20);
        &:last-of-type {
          margin-right: 0;
        }
        &.active {
          font-size: pr2(18);
          font-family: PingFangSC-Medium;
          font-weight: 700;
          color: rgba(17,30,54,1);
          position: relative;
          &:after {
            content: '';
            display: block;
            width: pr2(5);
            height: pr2(4);
            border-radius: pr2(5);
            background: rgba(0, 85, 255, 1);
            position: absolute;
            bottom: pr2(4);
            left: 50%;
            transform: translateX(-50%);
          }
        }
      }
    }
    .process_tab_wrapper {
      >li {
        display: flex;
        min-height: pr2(48);
        position: relative;
        .point {
          width: pr2(6);
          height: pr2(6);
          background: rgba(197, 202, 212, 1);
          border-radius: 50%;
          position: absolute;
          top: 3px;
          left: 0;
          &.active {
            background: rgba(0, 204, 136, 1);
            &:after {
              content: '';
              display: none;
            }
          }
          &:after {
            content: '';
            display: block;
            width: 1px;
            height: pr2(42);
            background: rgba(241, 243, 246, 1);
            position: absolute;
            bottom: pr2(6);
            left: 50%;
            transform: translateX(-50%);
          }
        }
        .right_content {
          padding-left: pr2(18);
          box-sizing: border-box;
          .process_name {
            font-size: pr2(14);
            font-weight: 400;
            font-family: PingFangSC-Regular;
            color: rgba(17,30,54,1);
            line-height: 1;
            &.active {
              font-family: PingFangSC-Medium;
              font-weight: 700;
            }
          }
          .process_time {
            font-size: pr2(12);
            font-family: PingFangSC-Regular;
            font-weight: 400;
            color: rgba(102,109,127,1);
            line-height: 1;
            margin-top: pr2(8);
          }
        }
      }
    }
    .live_tab_wrapper {
      width: 100%;
      .vehicle_workspace {
        width: 100%;
        display: flex;
        height: pr2(36);
        background:rgba(248,249,252,1);
        border-radius: pr2(4);
        align-items: center;
        justify-content: space-between;
        padding: 0 pr2(12);
        box-sizing: border-box;
        margin-bottom: pr2(16);
        >p {
          font-size: pr2(12);
          color: rgba(17,30,54,1);
          line-height: 1;
          &:first-of-type {
            font-family: PingFangSC-Regular;
            font-weight: 400;
          }
          &:last-of-type {
            font-family: PingFangSC-Medium;
            font-weight: 700;
          }
        }
      }
      .process_list_container {
        >li {
          margin-top: pr2(20);
          .live_process_title {
            font-size: pr2(16);
            font-family: PingFangSC-Medium;
            font-weight: 700;
            color: rgba(17,30,54,1);
            line-height: 1;
          }
          .live_process_time {
            font-size: pr2(12);
            font-family: PingFangSC-Regular;
            font-weight: 400;
            color: rgba(102,109,127,1);
            line-height: 1;
            margin-top: pr2(8);
          }
          .note_text {
            margin-top: pr2(12);
            font-size: pr2(14);
            font-family: PingFangSC-Regular;
            font-weight: 400;
            color: rgba(17,30,54,1);
            line-height: 1;
          }
          .process_list_wrapper {
            >li {
              font-size: pr2(14);
              font-family: PingFangSC-Regular;
              font-weight: 400;
              color: rgba(17,30,54,1);
              line-height: 1;
              &.baoyang_list {
                margin-top: pr2(20);
              }
              .baoyang_title {
                font-size: pr2(14);
                font-family: PingFangSC-Regular;
                font-weight: 400;
                color:rgba(17,30,54,1);
                line-height: 1;
              }
              .img_wrapper, .baoyang_img_list {
                width: pr2(223);
                // height: pr2(149);
                border-radius: pr2(4);
                margin-top: pr2(12);
                >div {
                  margin-top: pr2(12);
                }
                img {
                  width: 100%;
                  height: 100%;
                  border-radius: pr2(4);
                  @extend %athm-holdimg-white;
                }
              }
            }
          }
        }
      }
      .process_list_empty {
        display: flex;
        flex-direction: column;
        align-items: center;
        .empty_icon {
          width: pr2(160);
          height: pr2(160);
          background: url('data:image/png;base64,') no-repeat;
          background-size: 100% 100%;
        }
        .empty_text {
          font-size: pr2(17);
          font-family: PingFangSC-Regular;
          color: rgba(17,30,54,1);
          line-height: 1;
          margin-top: pr2(24);
        }
      }
    }
  }
  .construction_person_wrapper {
    width: 100%;
    box-shadow: 0 pr2(4) pr(20) 0 rgba(17,30,54,0.06);
    border-radius: pr2(4);
    padding: pr2(16) pr2(20);
    box-sizing: border-box;
    margin-top: pr2(20);
    position: relative;
    .construction_person_title {
      font-size: pr2(18);
      font-family: PingFangSC-Medium;
      font-weight: 700;
      color: rgba(17,30,54,1);
      line-height: 1;
      margin-bottom: pr2(16);
    }
    .construction_person_content {
      display: flex;
      .construction_person_portrait {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-right: pr2(12);
        img {
          display: block;
          width: pr2(50);
          height: pr2(50);
          border-radius: 50%;
          margin-bottom: pr2(4);
          @extend %athm-holdimg-white;
        }
        .level_label {
          padding: pr2(3);
          box-sizing: border-box;
          font-size: pr2(11);
          font-family: PingFangSC-Regular;
          font-weight: 400;
          color:rgba(255,255,255,1);
          line-height: 1;
          background: rgba(0, 85, 255, 1);
          border-radius: pr2(4) 0 pr2(4) 0;
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
      .construction_person_info {
        font-size: pr2(12);
        font-family: PingFangSC-Regular;
        font-weight: 400;
        color: rgba(102,109,127,1);
        line-height: 1;
        .name {
          font-size: pr2(16);
          font-family: PingFangSC-Medium;
          font-weight: 700;
          color:rgba(17,30,54,1);
          line-height: 1;
          margin-top: pr2(6);
        }
        .work_number {
          margin: pr2(11) 0;
        }
        .tel_num_wrapper {
          display: flex;
          .service_length {
            margin-right: pr2(40);
          }
        }
      }
    }
    .online_consulting_btn {
      width: pr2(76);
      height: pr2(32);
      position: absolute;
      right: pr2(20);
      top: 50%;
      transform: translateY(-50%);
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: pr2(14);
      font-family: PingFangSC-Regular;
      font-weight: 400;
      color: rgba(0,85,255,1);
      line-height: 1;
      &:after {
        content: '';
        position: absolute;
        width: 200%;
        height: 200%;
        top: 0;
        left: 0;
        border: 1px solid rgba(0, 85, 255, 0.2);
        transform: scale(0.5);
        transform-origin: 0 0;
        box-sizing: border-box;
        border-radius: 4px;
      }
    }
  }
}